'use client'

import { useState } from 'react'
import { useRouter } from 'next/navigation'
import { useCart } from '@/context/CartContext'
import { Button } from '@/components/ui/button'
import { Card, CardContent, CardHeader, CardTitle } from '@/components/ui/card'
import { Input } from '@/components/ui/input'
import { Label } from '@/components/ui/label'
import { Textarea } from '@/components/ui/textarea'
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from '@/components/ui/select'
import { Separator } from '@/components/ui/separator'
import { RadioGroup, RadioGroupItem } from '@/components/ui/radio-group'

interface FormData {
  // Datos personales
  firstName: string
  lastName: string
  email: string
  phone: string
  
  // Direcci√≥n de env√≠o
  address: string
  city: string
  state: string
  zipCode: string
  notes: string
  
  // M√©todo de pago
  paymentMethod: string
  cardNumber: string
  cardName: string
  cardExpiry: string
  cardCvv: string
}

export default function CheckoutPage() {
  const { state, clearCart } = useCart()
  const router = useRouter()
  const [currentStep, setCurrentStep] = useState(1)
  const [isProcessing, setIsProcessing] = useState(false)
  const [formData, setFormData] = useState<FormData>({
    firstName: '',
    lastName: '',
    email: '',
    phone: '',
    address: '',
    city: '',
    state: '',
    zipCode: '',
    notes: '',
    paymentMethod: 'credit-card',
    cardNumber: '',
    cardName: '',
    cardExpiry: '',
    cardCvv: ''
  })

  // Redirect if cart is empty
  if (state.items.length === 0) {
    router.push('/carrito')
    return null
  }

  const formatPrice = (price: number) => {
    return new Intl.NumberFormat('es-AR', {
      style: 'currency',
      currency: 'ARS'
    }).format(price)
  }

  const subtotal = state.total
  const shipping = subtotal > 25000 ? 0 : 2500
  const total = subtotal + shipping

  const handleInputChange = (field: keyof FormData, value: string) => {
    setFormData(prev => ({ ...prev, [field]: value }))
  }

  const validateStep = (step: number) => {
    switch (step) {
      case 1:
        return formData.firstName && formData.lastName && formData.email && formData.phone
      case 2:
        return formData.address && formData.city && formData.state && formData.zipCode
      case 3:
        if (formData.paymentMethod === 'credit-card') {
          return formData.cardNumber && formData.cardName && formData.cardExpiry && formData.cardCvv
        }
        return true
      default:
        return false
    }
  }

  const handleNextStep = () => {
    if (validateStep(currentStep)) {
      setCurrentStep(prev => prev + 1)
    }
  }

  const handlePreviousStep = () => {
    setCurrentStep(prev => prev - 1)
  }

  const handleSubmitOrder = async () => {
    if (!validateStep(3)) return

    setIsProcessing(true)

    try {
      // Simular procesamiento del pedido
      await new Promise(resolve => setTimeout(resolve, 3000))
      
      // Limpiar carrito
      clearCart()
      
      // Redirigir a p√°gina de confirmaci√≥n
      router.push('/checkout/confirmacion')
    } catch (error) {
      console.error('Error processing order:', error)
      alert('Hubo un error al procesar tu pedido. Por favor, int√©ntalo de nuevo.')
    } finally {
      setIsProcessing(false)
    }
  }

  const steps = [
    { number: 1, title: 'Datos Personales' },
    { number: 2, title: 'Direcci√≥n de Env√≠o' },
    { number: 3, title: 'M√©todo de Pago' }
  ]

  return (
    <div className="min-h-screen bg-gray-50">
      <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 py-8">
        {/* Header */}
        <div className="mb-8">
          <h1 className="text-3xl font-bold text-gray-900 mb-2">Checkout</h1>
          <p className="text-gray-600">Completa tu informaci√≥n para finalizar la compra</p>
        </div>

        {/* Progress Steps */}
        <div className="mb-8">
          <div className="flex items-center justify-center space-x-8">
            {steps.map((step, index) => (
              <div key={step.number} className="flex items-center">
                <div className={`flex items-center justify-center w-10 h-10 rounded-full border-2 ${
                  currentStep >= step.number 
                    ? 'bg-red-600 border-red-600 text-white' 
                    : 'border-gray-300 text-gray-300'
                }`}>
                  {step.number}
                </div>
                <span className={`ml-2 text-sm font-medium ${
                  currentStep >= step.number ? 'text-red-600' : 'text-gray-500'
                }`}>
                  {step.title}
                </span>
                {index < steps.length - 1 && (
                  <div className={`w-16 h-0.5 ml-4 ${
                    currentStep > step.number ? 'bg-red-600' : 'bg-gray-300'
                  }`} />
                )}
              </div>
            ))}
          </div>
        </div>

        <div className="grid grid-cols-1 lg:grid-cols-3 gap-8">
          {/* Form */}
          <div className="lg:col-span-2">
            <Card>
              <CardHeader>
                <CardTitle>{steps[currentStep - 1].title}</CardTitle>
              </CardHeader>
              <CardContent className="space-y-6">
                {/* Step 1: Personal Information */}
                {currentStep === 1 && (
                  <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
                    <div>
                      <Label htmlFor="firstName">Nombre *</Label>
                      <Input
                        id="firstName"
                        value={formData.firstName}
                        onChange={(e) => handleInputChange('firstName', e.target.value)}
                        placeholder="Tu nombre"
                      />
                    </div>
                    <div>
                      <Label htmlFor="lastName">Apellido *</Label>
                      <Input
                        id="lastName"
                        value={formData.lastName}
                        onChange={(e) => handleInputChange('lastName', e.target.value)}
                        placeholder="Tu apellido"
                      />
                    </div>
                    <div>
                      <Label htmlFor="email">Email *</Label>
                      <Input
                        id="email"
                        type="email"
                        value={formData.email}
                        onChange={(e) => handleInputChange('email', e.target.value)}
                        placeholder="tu@email.com"
                      />
                    </div>
                    <div>
                      <Label htmlFor="phone">Tel√©fono *</Label>
                      <Input
                        id="phone"
                        value={formData.phone}
                        onChange={(e) => handleInputChange('phone', e.target.value)}
                        placeholder="+54 11 1234-5678"
                      />
                    </div>
                  </div>
                )}

                {/* Step 2: Shipping Address */}
                {currentStep === 2 && (
                  <div className="space-y-4">
                    <div>
                      <Label htmlFor="address">Direcci√≥n *</Label>
                      <Input
                        id="address"
                        value={formData.address}
                        onChange={(e) => handleInputChange('address', e.target.value)}
                        placeholder="Calle y n√∫mero"
                      />
                    </div>
                    <div className="grid grid-cols-1 md:grid-cols-3 gap-4">
                      <div>
                        <Label htmlFor="city">Ciudad *</Label>
                        <Input
                          id="city"
                          value={formData.city}
                          onChange={(e) => handleInputChange('city', e.target.value)}
                          placeholder="Ciudad"
                        />
                      </div>
                      <div>
                        <Label htmlFor="state">Provincia *</Label>
                        <Select onValueChange={(value) => handleInputChange('state', value)}>
                          <SelectTrigger>
                            <SelectValue placeholder="Seleccionar" />
                          </SelectTrigger>
                          <SelectContent>
                            <SelectItem value="CABA">CABA</SelectItem>
                            <SelectItem value="Buenos Aires">Buenos Aires</SelectItem>
                            <SelectItem value="C√≥rdoba">C√≥rdoba</SelectItem>
                            <SelectItem value="Santa Fe">Santa Fe</SelectItem>
                            <SelectItem value="Mendoza">Mendoza</SelectItem>
                          </SelectContent>
                        </Select>
                      </div>
                      <div>
                        <Label htmlFor="zipCode">C√≥digo Postal *</Label>
                        <Input
                          id="zipCode"
                          value={formData.zipCode}
                          onChange={(e) => handleInputChange('zipCode', e.target.value)}
                          placeholder="1234"
                        />
                      </div>
                    </div>
                    <div>
                      <Label htmlFor="notes">Notas adicionales (opcional)</Label>
                      <Textarea
                        id="notes"
                        value={formData.notes}
                        onChange={(e) => handleInputChange('notes', e.target.value)}
                        placeholder="Instrucciones especiales para la entrega..."
                        rows={3}
                      />
                    </div>
                  </div>
                )}

                {/* Step 3: Payment Method */}
                {currentStep === 3 && (
                  <div className="space-y-6">
                    <div>
                      <Label>M√©todo de Pago</Label>
                      <RadioGroup
                        value={formData.paymentMethod}
                        onValueChange={(value) => handleInputChange('paymentMethod', value)}
                        className="mt-2"
                      >
                        <div className="flex items-center space-x-2">
                          <RadioGroupItem value="credit-card" id="credit-card" />
                          <Label htmlFor="credit-card">Tarjeta de Cr√©dito/D√©bito</Label>
                        </div>
                        <div className="flex items-center space-x-2">
                          <RadioGroupItem value="mercado-pago" id="mercado-pago" />
                          <Label htmlFor="mercado-pago">Mercado Pago</Label>
                        </div>
                        <div className="flex items-center space-x-2">
                          <RadioGroupItem value="transfer" id="transfer" />
                          <Label htmlFor="transfer">Transferencia Bancaria</Label>
                        </div>
                      </RadioGroup>
                    </div>

                    {formData.paymentMethod === 'credit-card' && (
                      <div className="space-y-4">
                        <div>
                          <Label htmlFor="cardNumber">N√∫mero de Tarjeta *</Label>
                          <Input
                            id="cardNumber"
                            value={formData.cardNumber}
                            onChange={(e) => handleInputChange('cardNumber', e.target.value)}
                            placeholder="1234 5678 9012 3456"
                          />
                        </div>
                        <div>
                          <Label htmlFor="cardName">Nombre en la Tarjeta *</Label>
                          <Input
                            id="cardName"
                            value={formData.cardName}
                            onChange={(e) => handleInputChange('cardName', e.target.value)}
                            placeholder="Como aparece en la tarjeta"
                          />
                        </div>
                        <div className="grid grid-cols-2 gap-4">
                          <div>
                            <Label htmlFor="cardExpiry">Vencimiento *</Label>
                            <Input
                              id="cardExpiry"
                              value={formData.cardExpiry}
                              onChange={(e) => handleInputChange('cardExpiry', e.target.value)}
                              placeholder="MM/AA"
                            />
                          </div>
                          <div>
                            <Label htmlFor="cardCvv">CVV *</Label>
                            <Input
                              id="cardCvv"
                              value={formData.cardCvv}
                              onChange={(e) => handleInputChange('cardCvv', e.target.value)}
                              placeholder="123"
                            />
                          </div>
                        </div>
                      </div>
                    )}

                    {formData.paymentMethod === 'mercado-pago' && (
                      <div className="p-4 bg-blue-50 rounded-lg">
                        <p className="text-sm text-blue-800">
                          Ser√°s redirigido a Mercado Pago para completar el pago de forma segura.
                        </p>
                      </div>
                    )}

                    {formData.paymentMethod === 'transfer' && (
                      <div className="p-4 bg-green-50 rounded-lg">
                        <p className="text-sm text-green-800 mb-2">
                          Datos para transferencia bancaria:
                        </p>
                        <div className="text-sm space-y-1">
                          <p><strong>Banco:</strong> Banco Ejemplo</p>
                          <p><strong>CBU:</strong> 1234567890123456789012</p>
                          <p><strong>Alias:</strong> POWERFIT.SUPLEMENTOS</p>
                        </div>
                      </div>
                    )}
                  </div>
                )}

                {/* Navigation Buttons */}
                <div className="flex justify-between pt-6">
                  <Button
                    variant="outline"
                    onClick={handlePreviousStep}
                    disabled={currentStep === 1}
                  >
                    Anterior
                  </Button>
                  
                  {currentStep < 3 ? (
                    <Button
                      onClick={handleNextStep}
                      disabled={!validateStep(currentStep)}
                      className="bg-red-600 hover:bg-red-700"
                    >
                      Siguiente
                    </Button>
                  ) : (
                    <Button
                      onClick={handleSubmitOrder}
                      disabled={!validateStep(3) || isProcessing}
                      className="bg-red-600 hover:bg-red-700"
                    >
                      {isProcessing ? 'Procesando...' : 'Finalizar Compra'}
                    </Button>
                  )}
                </div>
              </CardContent>
            </Card>
          </div>

          {/* Order Summary */}
          <div className="lg:col-span-1">
            <Card className="sticky top-4">
              <CardHeader>
                <CardTitle>Resumen del Pedido</CardTitle>
              </CardHeader>
              <CardContent className="space-y-4">
                {/* Products */}
                <div className="space-y-3">
                  {state.items.map((item) => (
                    <div key={item.id} className="flex justify-between text-sm">
                      <div className="flex-1">
                        <p className="font-medium">{item.nombre}</p>
                        <p className="text-gray-600">Cantidad: {item.quantity}</p>
                      </div>
                      <span className="font-medium">
                        {formatPrice(item.precio * item.quantity)}
                      </span>
                    </div>
                  ))}
                </div>

                <Separator />

                {/* Totals */}
                <div className="space-y-2">
                  <div className="flex justify-between text-sm">
                    <span>Subtotal</span>
                    <span>{formatPrice(subtotal)}</span>
                  </div>
                  <div className="flex justify-between text-sm">
                    <span>Env√≠o</span>
                    <span className={shipping === 0 ? 'text-green-600' : ''}>
                      {shipping === 0 ? 'GRATIS' : formatPrice(shipping)}
                    </span>
                  </div>
                </div>

                <Separator />

                <div className="flex justify-between font-bold text-lg">
                  <span>Total</span>
                  <span className="text-red-600">{formatPrice(total)}</span>
                </div>

                {/* Security Info */}
                <div className="pt-4 border-t text-sm text-gray-600 space-y-1">
                  <div className="flex items-center gap-2">
                    <span>üîí</span>
                    <span>Pago 100% seguro</span>
                  </div>
                  <div className="flex items-center gap-2">
                    <span>üöö</span>
                    <span>Env√≠o en 24-48hs</span>
                  </div>
                  <div className="flex items-center gap-2">
                    <span>‚Ü©Ô∏è</span>
                    <span>30 d√≠as para devoluciones</span>
                  </div>
                </div>
              </CardContent>
            </Card>
          </div>
        </div>
      </div>
    </div>
  )
}
